*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}



body{
	background: #fff;
	
}




.primary__btn::before{
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	background: #171717;
	width: 100%;
	height: 1px;
}


.get-in-touch::before{
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	background: #fff;
	width: 100%;
	height: 1px;
}



.primary__text::before{
	content: "";
	display: inline-flex;
	position: absolute;
	top:25%;
	right:-20px;
	background: #171717;
	width: 200px;
	height: 1px;
}



.line__1,
.line__2,
.line__3{

position: fixed;
top: 0;
height: 100%;
width: 1px;
border: 1px solid #e9e9e9;
z-index: -1;
    
}

.line__1{
left: calc(100% - 75%);
}

.line__2{

left: calc(100% - 49.2%);
}


.line__3{
left: calc(100% - 24.8%);
}









:root {
	/* fonts */
	--h2: "Neue Haas Grotesk Display Pro";
	--h1: Cinzel;
  
	/* font sizes */
	--p-size: 12px;
	--font-size-5xs: 8px;
	--font-size-9xl: 28px;
	--font-size-base: 16px;
	--h2-size: 40.5px;
	--h1-size: 136.7px;
	--font-size-83xl: 102px;
	--font-size-37xl: 56px;
  
	/* Colors */
	--color-white: #fff;
	--color-gray-100: #242424;
	--color-gray-200: #171717;
	--color-black: #000;
	--color-dimgray-100: #5e5e5e;
  
	/* Gaps */
	--gap-49xl: 68px;
	--gap-21xl: 40px;
	--gap-5xl: 24px;
	--gap-10xs: 3px;
  
	/* Paddings */
	--padding-3xs: 10px;
	--padding-281xl: 300px;
  }
  